<template>
  <div class="manage">
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      width="50%"
      :before-close="handleClose"
    >
      <!-- 用户表单 -->
      <el-form
        ref="form"
        :model="form"
        label-width="80px"
        :inline="true"
        :rules="rules"
      >
        <el-form-item label="姓名" prop="name">
          <el-input v-model="form.name" placeholder="请输入姓名"></el-input>
        </el-form-item>
        <el-form-item label="年龄" prop="age">
          <el-input v-model="form.age" placeholder="请输入年龄"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="sex">
          <el-select v-model="form.sex" placeholder="请选择性别">
            <el-option label="男" :value="1"></el-option>
            <el-option label="女" :value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="出生日期" prop="birth">
          <el-date-picker
            v-model="form.birth"
            type="date"
            placeholder="选择日期"
            value-format="yyyy-MM-DD"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label="地址" prop="addr">
          <el-input v-model="form.addr" placeholder="请输入地址"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </span>
    </el-dialog>
    <div class="manage-header">
      <el-button type="primary" @click="addList">
        + 新增
      </el-button>
      <el-form :model="userForm"  :inline="true">
        <el-form-item>
          <el-input v-model="userForm.name" placeholder="请输入名称"></el-input>
        </el-form-item>
        <el-form-item>
         <el-button type="primary" @click="onSubmit">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="common">
      <el-table :data="tableData" style="width: 100%" height="90%" stripe>
        <el-table-column prop="name" label="姓名" width="180">
        </el-table-column>
        <el-table-column prop="sex" label="性别" width="180">
          <template slot-scope="scope">
             <span style="margin-left: 10px">{{ scope.row.sex === 1 ?'男':'女' }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="age" label="年龄"> </el-table-column>
         </el-table-column>
        <el-table-column prop="birth" label="出生日期"> </el-table-column>
         </el-table-column>
        <el-table-column prop="addr" label="地址"> </el-table-column>
         </el-table-column>
        <el-table-column prop="address" label="操作">
          <template slot-scope="scope">
              <el-button size="mini" @click="handleEdit(scope.row)">编辑</el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="pager">
         <el-pagination
          layout="prev, pager, next"
          :total="total"
          @current-change="changePage"
          :page-size="pageData.limit"
          >
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { addUser, getUser ,editUser ,delUser} from '../api'
export default {
  data() {
    return {
      dialogVisible: false,
      form: {
        name: "",
        addr: "",
        age: "",
        birth: "",
        sex: "",
      },
      rules: {
        name: [
          {
            required: true,
            message: "请输入姓名",
          },
        ],
        age: [
          {
            required: true,
            message: "请输入年龄",
          },
        ],
        sex: [
          {
            required: true,
            message: "请输入性别",
          },
        ],
        birth: [
          {
            required: true,
            message: "请输入日期",
          },
        ],
        addr: [
          {
            required: true,
            message: "请输入地址",
          },
        ],
      },
       tableData:[],
       modalType:0,
       total:0,
       pageData:{
        page:1,
        limit:8
       },
       userForm:{
        name:''
       }
    };
  },
  methods: {
    submit() {
      this.$refs.form.validate((valid) => {
        // console.log(this.form);
        // console.log(valid);
        if (valid) {
          if(this.modalType === 0){
             addUser(this.form).then(()=>{
                this.getList()
             })
          }else{
            editUser(this.form).then(()=>{
              this.getList()
            })
          }
         this.handleClose()
        }
      });
    },
    addList(){
      this.modalType = 0
      this.dialogVisible = true
    },
    handleClose() {
      console.log(this.$refs.form);
      this.$refs.form.resetFields();
      this.dialogVisible = false;
    },
    cancel() {
      this.handleClose();
    },
    getList(){
       getUser({params:{...this.userForm,...this.pageData}}).then(({data})=>{
      console.log(data);
      this.tableData = data.list
      this.total = data.count || 0
    })
    },
    handleEdit(row){
      this.modalType = 1
      this.dialogVisible = true
      //要对数据深拷贝，否则会报错
      // this.form = JSON.parse(JSON.stringify(row))
      // 在数据动态更新后，dom未及时更新,使用nextTick获取数据更新后最新dom的变化
      this.$nextTick(()=>{
         this.form = JSON.parse(JSON.stringify(row))
      })
    },
    handleDelete(row){
       this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
           delUser({id:row.id}).then(()=>{
              this.$message({
              type: 'success',
              message: '删除成功!'
              });
              this.getList()
           })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
    },
    changePage(val){
      this.pageData.page = val
      this.getList()
    },
    onSubmit(){
      this.getList()
    }
  },
  mounted () {
    this.getList()
  }
};
</script>

<style lang="less" scoped>
.manage{
  height: 90%;
  .manage-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .el-table::before{
    height: 0;
  }
  .common{
    position: relative;
    height: calc(100% - 62px);
    .pager{
      position: absolute;
      bottom: 0;
      right: 20px;
    }
  }
}
</style>
